    <div class="noprn"> ECharts </div>
    <div id="main" style="width: 600px;height:400px;"></div>
    <div class="noprn" id="printImg"></div>
    <div>
        12312313213123213213213213213213
    </div>
</template>

<script>
    import $ from 'jquery'
    //import axios from "axios";
    import * as echarts from 'echarts'
    //import 'nanobar'

    // //
    // const options = {
    //     target: $("#nanobar")
    // };
    // //
    // const nanobar = new Nanobar( options );

    export default {
        name: "Waiting",
        data(){
            return {

            }
        },
        created() {
            // const interval=setInterval(function(){
            //     axios.request({
            //         method:'post',
            //         url:'',
            //         dataType:'text',
            //     }).then(response => {
            //         $("#tips").innerHTML="Analysing, please wait..."
            //         $("#nanobar").fadeIn()
            //         nanobar.go(response)
            //     })
            // },1000)
            //
            // axios.request({
            //     method:'post',
            //     url:'',
            //
            // })

            // 
            const option = {
                title: {
                    text: ' ECharts '
                },
                tooltip: {},
                toolbox: {
                    show: true,
                    right: '20px',
                    feature: {
                        // 
                        saveAsImage: {
                            show: true,
                            title: ''
                        }
                    }
                },
                // falseecharts
                animation: false,
                legend: {
                    data: ['']
                },
                xAxis: {
                    data: ["", "", "", "", "", ""]
                },
                yAxis: {},
                series: [{
                    name: '',
                    type: 'bar', // line
                    data: [5, 20, 36, 10, 10, 20],
                    markPoint: {
                        data: [{
                            type: "max",
                            name: "Max"
                        }, {
                            type: "min",
                            name: "Min"
                        }]
                    },
                    markLine: {
                        data: [{
                            type: "average",
                            name: "Avg"
                        },
                            [{
                                symbol: "none",
                                x: "90%",
                                yAxis: "max"
                            }, {
                                symbol: "emptyCircle", //
                                label: {
                                    position: "start",
                                    formatter: "Max"
                                },
                                type: "max",
                                name: "Top"
                            }]
                        ]
                    }
                }]
            };
            $(document).ready(function(){

                const myChart = echarts.init(document.getElementById('main'));
                myChart.setOption(option)

                const printBtn = document.getElementById('printImg');
                const canvas = document.querySelector("#main canvas")
                console.log(canvas)
                const dataUrl = canvas.toDataURL();

                printBtn.onclick = function () {
                    //doPrintByFrame(dataUrl);
                    doViewAndPrint(dataUrl);
                    // 
                    // window.print();
                };

                // window.print
                // 
                function doViewAndPrint(canvasUrl) {
                    const domPrint = document.createElement('img');
                    domPrint.src = canvasUrl;
                    let page = window.open('', '_blank'); // 
                    page.document.body.appendChild(domPrint);

                    domPrint.onload = function () {
                        page.print(); // 
                        page.close(); // 
                    };
                }
            })


            // iframeiframe
            function doPrintByFrame(canvasUrl) {
                //iframeiframe
                var iframe = document.getElementById("print-iframe");
                const domPrint = document.createElement('img');
                domPrint.src = canvasUrl;
                if (!iframe) {
                    iframe = document.createElement('IFRAME');
                    var doc = null;
                    iframe.setAttribute("id", "print-iframe");
                    iframe.setAttribute('style', 'position:absolute;width:0px;height:0px;left:-500px;top:-500px;');
                    document.body.appendChild(iframe);
                    doc = iframe.contentWindow.document;
                    //
                    //doc.write("<LINK rel="stylesheet" type="text/css" href="css/print.css">");
                    doc.body.appendChild(domPrint);
                    doc.close();
                    iframe.contentWindow.focus();
                }
                domPrint.onload = function () {
                    iframe.contentWindow.print(); // 
                    document.body.removeChild(iframe);
                };
            }
        }
    }
</script>

